.palette-menu-current-item {
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ff1e09+0,ec1400+34,d01100+100 */
background: rgb(255,30,9); /* Old browsers */
background: -moz-linear-gradient(top,  rgba(255,30,9,1) 0%, rgba(236,20,0,1) 34%, rgba(208,17,0,1) 100%); /* FF3.6-15 */
background: -webkit-linear-gradient(top,  rgba(255,30,9,1) 0%,rgba(236,20,0,1) 34%,rgba(208,17,0,1) 100%); /* Chrome10-25,Safari5.1-6 */
background: linear-gradient(to bottom,  rgba(255,30,9,1) 0%,rgba(236,20,0,1) 34%,rgba(208,17,0,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff1e09', endColorstr='#d01100',GradientType=0 ); /* IE6-9 */
}


