.entry-header__events-archive {
    @extend .widget__event;
    &-wrapper {
        padding-left: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        &-title {
            text-align: justify;
            text-transform: uppercase;
            a {
                color: #484848;
                &:hover {
                    color: #000;
                }
            }
        }
        &-place {
            @extend h4;
            font-family: $Site__font-regular;
            margin-bottom: 0px;
        }
    }
}

.entity {
    width: 100%;
    display: flex;
    flex-direction: row;
    //justify-content: space-around;
    align-content: stretch;
    align-items: stretch;
    &__item {
        width: 250px;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: stretch;
        padding: 10px;
        align-content: center;
        background: #b7b9e0;
        &-img {
            & img {
                border-radius: 50%;
                display: block;
                border: 3px solid #000;
                width: 200px!important;
                height: auto;
            }
        }
        &-description {
            font-family: $title-font;
            text-align: center;
            padding: 10px 0px;
            text-transform: uppercase;
        }
    }
}


